body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image-container {
    padding: 0px;
    width: 150px;
    height: 150px;
    overflow: hidden;
    border: 5px rgb(89, 89, 89) solid;
    border-radius: 50%;
    display: inline-block;
    margin: 15px;
    text-align: center;
    box-shadow: 0px 0px 5px rgb(234, 255, 159);
}

.ind-img {
    width: 160px;
}
* {
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
    letter-spacing: 2px
}

.header {
    /*background-color: rgb(0, 125, 208);*/
    background-color: aqua;
    height: 70px;
    text-align: center;
    padding-top: 14px;
    font-family: arial;
    font-weight: 400;
    color: white;
    box-shadow: -2px 1px 10px rgb(115, 125, 160)
}

#home, #center-text {
    display: inline-block;
    margin-right: 60px;
}

#home {
    color: rgb(255, 255, 255);
    font-weight: 1000;
    font-size: 20px
}

#score {
    color: black;
    display: inline-block;
    background-color: coral;
    border-radius: 40px;
    padding: 10px 15px;
    box-shadow: 0px 0px 9px rgb(57, 98, 160);
    font-weight: 600;
}

p {
    display: inline-block;
}

#hs {
    margin-left: 15px;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
